@charset "UTF-8";
// NHA-COMMON-EVENT_Detail-065
.default_list_type01 {
    .kind {
        width:fit-content; font-size:12px; display:block; padding:5px 12px; margin:0 0 0 3px; font-weight:$medium;
        &:first-of-type {margin:0;}
        @include borderRadius(10px);
        &.variety_1 {color:$blue6;background-color:#e2e6fd;}
        &.variety_2 {color:$blue7;background-color:#d9d9ec;}
        &.variety_3 {color:$blue5;background-color:$blue3;}
    }
    .title_txt {
        width:fit-content; margin:0 auto;
        strong {font-size:30px;font-weight:$medium;color:$gray7;}
        ul {
            width:fit-content; padding:40px 0;margin:0 auto;
            @extend %clearfix;
        }
        li {
            float:left; padding:0 0 0 50px; display:flex; align-items:center;
            &:first-of-type { padding:0; }
            p {font-size:14px;color:$gray5;font-weight:$regular;padding:0 0 0 10px;}
        }
    }
    .view_wrap {
        padding:40px 0 0; border-top:1px solid #e5e5e5;
        img { width:100%; }
    }
    @include mob () {
        padding:0 em(30);
        .kind {
            width:fit-content; font-size:em(22); padding:em(12) em(17) em(8); font-weight:$bold;
            &:first-of-type {margin:0;}
            @include borderRadius(em(70));
        }
        .title_txt {
            strong {font-size:em(40);font-weight:$bold; line-height:em(54);}
            ul {padding:em(45) 0; margin:em(-12) 0 0;}
            li {
                float:left; padding:em(13) em(70) 0 0;
                &:first-of-type { padding:em(13) em(70) 0 0; }
                p {font-size:em(26);color:$gray5;font-weight:$regular;padding:0 0 0 10px;}
            }
        }
        .view_wrap {
            padding:40px 0 0; border-top:1px solid #e5e5e5;
            img { width:100%; }
        }
    }
}