// NHA-RETAIL-ORDER-002
.product_contents {
    width:100%; margin:0 auto;
    .swiper-button-next:after, .swiper-button-prev:after {display:none;}
}
.product_detail_type {
    // 메뉴 고정
    &.view_active {
        .fixed_menu {width:100%; position:fixed; top:80px; z-index:1;}
        .product_top_menu { display:block; }
        .substance_wrap {margin:80px 0 0;}
    }
    // 가격 푸터 고정
    &.price_active {
        #ProductPriceBar {position:absolute;bottom:-145px;}
    }
    // 옵션 고정
    &.option_active {
        #optionBar {position:fixed;top:280px;}
    }
    .view_detail {max-width:1200px;margin:0 auto;display:flex;padding:80px 0;}
    // slide img
    .detail_img {
        position:relative; width:100%; max-width:600px; height:600px; background-color:$gray1;
        @include borderRadius(30px);
        .slide {display:flex;align-items:center;justify-content:center;margin:0 auto;}
        .swiper-button-next, .swiper-button-prev {
            width:52px; height:52px;background:url(./../../images/common/slide_btn.png) 50% 50% no-repeat;
            background-size:cover; text-indent:-999999px;
        }
        .swiper-button-next {right:20px;}
        .swiper-button-prev {left:20px;@include transform(rotate(180deg));}
        .indicator {
            width:100%;bottom:-30px;text-align:center;position:absolute;z-index:1;
            .swiper-pagination-bullet {margin:0 4px;background-color:#e3e3e3;opacity:1;}
            .swiper-pagination-bullet-active {background-color:$gray5;}
        }
