        // 옵션 바
        #optionBar {
            display:none; width:288px; height:713px; position:absolute; top:0; right:0; background-color:$white; z-index:1;
            @include boxSizing;
            @include boxShadow(0 0 24px rgba(15, 13, 13, .2));
            .option_title {
                border-bottom:1px solid $gray2; padding:0 37px;
                .default_box {
                    padding:27px 0 26px; position:relative;
                    h3 {font-size:20px; font-weight:$medium; color:$gray7;}
                    .btn_clearFilters {
                        position:relative;font-size:13px; line-height:24px;font-weight:$medium; color:$gray7;margin:15px 0 0;
                        &:after {width:100%; height:1px; position:absolute; bottom:3px; left:0; background-color:$gray7; content:"";}
                        &:hover, &:focus {
                            color:$blue5;
                            &:after {background-color:$blue5;}
                        }
                    }
                }
                .availability_box {
                    border-top:1px solid $gray2; padding:28px 0;
                    strong {font-size:20px; margin:0 0 20px; font-weight:$medium; color:$gray7; display:block;}
                    p {
                        display:flex; justify-content:space-between;
                        button {
                            width:49%; font-size:13px; line-height:36px; border-radius:30px; border:1px solid $gray3; color:$gray7; font-weight:$medium;
                            @include transition();
                            &:hover, &:focus, &.on {
                                color:$white; background-color:$gray7; border-color:$gray7;
                            }
                        }
                    }
                }
            }
            .option_wrap {
                width:100%; position:absolute; top:113px; left:0;padding:10px 10px;overflow-y:auto; background-color:$white;
                @include boxSizing;
            }
            .scroll_bar_custom {padding:0;}
            .option_cont {
                height:350px;
                @include boxSizing;
                .option_type {
                    position:relative; padding:0 0 35px;
                    @include boxSizing;
                    &:last-of-type {
                        &:after {display:none;}
                    }
                    &:after {
                        width:212px; height:1px; background-color:#e3e3e3; content:"";display:block;
                        position:absolute; bottom:0; left:50%;
                        @include transform(translate(-50%, 0));
                    }
                    strong {display:block; padding:20px 0 0 25px; font-size:16px;font-weight:$medium;color:$gray7;}
                    ul {padding:20px 15px 0 25px;}
                    li {
                        font-size:14px;color:$gray5;font-weight:$regular; line-height:22px;position:relative; padding:0 0 0 10px;
                        &:before {
                            content:"·";display:inline-block;padding:0 5px 0 0;
                            font-weight:$extra-bold;position:absolute; left:0;
                        }
                    }
                    .code_row {
                        margin:30px 25px 0 25px; font-size:14px;color:$gray7;font-weight:$medium;
                        display:flex;justify-content:space-between;
                        @include boxSizing;
                    }
                }
            }
            .stock_message {
                width:100%; padding:60px 20px 0;
                @include boxSizing;
                span {
                    display:block;width:100%; background-color:#f5f5f5;font-size:13px;font-weight:$medium;
                    padding:10px 10px 10px 35px; line-height:16px;position:relative;
                    &:before {
                        width:17px; height:17px;content:"i";color:$white;background-color:#cecece;left:10px;
                        display:block;text-align:center;font-weight:$regular;font-size:12px;position:absolute;
                        @include borderRadius(20px);
                    }
                    @include boxSizing;
                    @include borderRadius(10px);
                    &.in_stock_txt {color:$blue5;}
                    &.not_stock_txt {color:#ea4335;}
                }
            }
        }
    }
}
// NHA-COMMON-PARTS_DETAIL(ORDER)-012
.product_detail_type {
    &.type01 {
        .info_form_wrap {
            max-width:450px;
            .variety {font-size:18px;font-weight:$medium;color:$blue7;}
            strong {padding:10px 0 0;font-size:40px;font-weight:$medium;}
        }
        .info_txt_wrap {
            padding:30px 0 0;
            & > li {
                display:flex;padding:20px 0;justify-content:space-between;border-top:1px solid #e3e3e3;
                font-size:16px;color:$gray5;line-height:25px;font-weight:$medium;
                span {
                    width:60%; min-width:60%;
                    &:last-of-type {width:40%; min-width:40%;text-align:right;padding:0 15px 0 0;}
                }
                .snb_s {
                    width:100%;
                    a {
                        color:$gray5; display:block; width:100%; margin:0 0 20px; position:relative;
                        &.on {
                            &:after {@include transform(rotate(180deg));}
                        }
                        &:after {
                            width:13px; height:8px;content:"";display:block;
                            background:url(./../../images/common/select_arr_default.png) 50% 50% no-repeat;
                            background-size:cover; position:absolute; right:11px; top:50%;
                            @include transform(translate(0, -50%));
                            @include transition(all, .2s);
                        }
                    }
                }
                .hidden_elm {
                    width:100%;
                    li {
                        padding:15px 30px;background-color:#f5f5f5;font-size:14px;color:#4d4e4f;font-weight:$regular;
                        @include borderRadius(10px);
                        @include boxSizing;
                    }
                }
            }
        }
        .product_top_menu {
            height:em(170);
            span { color:$blue7; }
        }
        @include mob () {
            .info_form_wrap {
                max-width:inherit; padding:em(40) em(30) em(70);
                .variety {font-size:em(30);font-weight:$bold;}
                strong {padding:em(20) 0 0;font-size:em(64);font-weight:$bold;}
                .desc {max-width:100%; padding:em(60) 0 0;font-size:em(32);line-height:em(42);font-weight:$bold;}
            }
            .info_txt_wrap {
                padding:em(50) 0 0;
                & > li {
                    padding:em(30) 0; border-top:em(2) solid #e3e3e3; font-size:em(32);color:$gray5;line-height:em(42);font-weight:$bold;
                    span {
                        &:last-of-type { padding:0 15px 0 0; }
                    }
                    .snb_s {
                        width:100%;
                        a {
                            color:$gray5; display:block; width:100%; margin:0; position:relative;
                            &.on {
                                &:after {@include transform(rotate(180deg));}
                            }
                            &:after {
                                width:em(24); height:em(15);
                                background:url(./../../images/common/select_arr_default_m.png) 50% 50% no-repeat;
                                background-size:cover;
                            }
                        }
                    }
                    .hidden_elm {
                        li {
                            padding:em(30) em(35);font-size:em(28);font-weight:$medium;
                            @include borderRadius(em(10));
                            @include boxSizing;
                        }
                    }
                }
            }
        }
    }
}
