// NHA-COMMON-INDIVIDUAL_PRICE(P)-058_H
.popup.individual_price_popup {
    .popup_content {
        max-width:1258px; background-color:$white; padding:70px 60px; text-align:center;
        @include borderRadius(27px);
        .pop_title {font-size:30px;font-weight:$medium;color:$gray7;}
        .option_wrap {
            text-align:left;
            > p {font-size:20px;font-weight:$medium;color:$gray7;}
        }
        .scroll_wrap {padding:0; overflow-y:hidden; display:flex; flex-wrap:wrap; justify-content: space-between;}
        .in_wrap {
            padding:25px 60px; margin:20px 0 0; border:1px solid $gray2;
            @include borderRadius(10px);
            .column_list_option {
                &:first-of-type {
                    > div {padding:0 0 40px;}
                }
                &:last-of-type {
                    > div {border-top:1px solid $gray2; padding:40px 0 0;}
                }
            }
        }
        .column_list_option {
            width:30.94%;
            span {font-size:16px;color:$gray7;font-weight:$medium; line-height:32px;}
            ul {padding:10px 0 0;}
            li {
                position:relative;font-size:14px;color:$gray5;font-weight:$regular;padding:0 0 0 10px;line-height:22px;
                &:before {content:"·";display:block;left:0;font-weight:$extra-bold;position:absolute;}
            }
        }
        .row_list_option {
            width:65.14%;
            ul {display:flex; justify-content:space-between;}
            .last_price_txt {
                width:100%; max-width:110px;
                p {font-size:18px;color:$blue5;line-height:22px;}
            }
            span {font-size:16px;color:$gray7;font-weight:$medium; line-height:32px;}
            p {padding:10px 0 0;font-size:14px;color:$gray7;font-weight:$medium;}
        }
    }
    .btn_popup_close {
        width:20px; height:20px; background-image:url(../../images/common/close_x_btn.png); right:26px; top:28px;
    }

    @include mob(){
        text-align:center;
        .popup_content {
            max-width:750px; padding:em(100) em(30); margin:0 em(30); display:inline-block;
            @include borderRadius(em(24));
            .pop_title {font-size:em(48); font-weight:$bold;}
            .option_wrap {
                margin:em(90) 0 0;
                > p {font-size:em(36); font-weight:$bold;}
            }
            .scroll_wrap {height:em(400); padding:0; margin:0 em(10) 0 0; overflow-y:scroll;}
            .in_wrap {
                padding:0; margin:em(24) 0 0; border:em(2) solid $gray3; overflow:hidden;
                @include borderRadius(em(16));
                .column_list_option {
                    &:first-of-type {
                        > div {padding:em(54) em(58) em(50);}
                    }
                    &:last-of-type {
                        > div {border-top:em(2) solid $gray2; padding:em(56) em(58) em(54);}
                    }
                }
            }
            .column_list_option {
                width:100%;
                > div {position:relative;}
                span {font-size:em(32); line-height:em(36); position:absolute;}
                ul {padding:0 0 0 em(260);}
                li {font-size:em(28); padding:0 0 0 em(20); line-height:em(36);}
            }
            .row_list_option {
                width:100%; background-color:#fafafa; padding:em(38) em(58) em(32); box-sizing:border-box;
                ul {display:block;}
                li {
                    margin:em(30) 0 0;
                    &:after {content:""; clear:both; display:block;}
                    &:first-child {margin:0;}
                }
                .last_price_txt {
                    max-width:100%;
                    p {font-size:em(36); line-height:em(46);}
                }
                span {font-size:em(28); color:$gray6; line-height:em(36); float:left;}
                p {padding:0; font-size:em(28); float:right;}
            }
        }
        .btn_popup_close {width:em(72); height:em(72); background-image:url(../../images/common/close_x_btn_m.png); right:em(22); top:em(22);}
    }
}
















