@charset "UTF-8";
// NHA-COMMON-FAQ-046
.faq_list_type01 {
    .faq_list_wrap {
        padding:25px 45px 0 0; border:1px solid transparent; border-bottom-color:$gray2;
        @include boxSizing;
        @include transition(border, .2s);
        &.active {border-color:$gray7;}
        &:last-of-type {
            border-bottom-color:transparent;
            &.active {border-bottom-color:$gray7;}
        }
        //&:hover, &:focus {border-color:$gray7;}
    }
    .row_list {
        display:flex;
        .tit_txt {font-size:14px; color:$gray5; font-weight:$medium;}
        .question_txt, .desc {padding:16px 0 30px;font-size:18px;color:$gray7;font-weight:$regular;line-height:26px;}
        .question_txt {width:800px; position:relative;}
        .num_m { display:none; }
        .snb_s {
            width:100%; padding:0 0 0 20px;
            a {
                display:block; width:100%; position:relative;
                &.on {
                    .toggle_icon {
                        @include transform(rotate(180deg));
                    }
                }
            }
        }
        .toggle_icon {
            width:13px; height:8px;display:block; background-size:cover; position:absolute;
            right:0; top:45%; margin:-10px 0 0; background:url(./../../images/common/select_arr_02.png) 50% 50% no-repeat;
            @include transition(all, .3s);
        }
        .num_txt {width:100%;max-width:95px; text-align:center;}
        .hidden_elm {
            p {
                position:relative; padding:30px 0;font-size:18px;font-weight:$regular;color:$gray7; line-height:26px;
                &:after {width:100%; height:1px;content:"";display:block;position:absolute; top:0;background-color:$gray2;}
            }
        }
        .answer_txt {
            padding:16px 0 30px;font-size:18px;color:$gray7;font-weight:$regular; display:flex; align-items:start;
            em {
                min-width:28px; min-height:28px;display:block;font-size:14px;font-weight:$extra-bold;line-height:25px;
                text-align:center;color:$blue5;background-color:#dcebfe; margin:0 10px 0 0;
                @include borderRadius(50px);
            }
        }
    }
    @include mob() {
        padding:em(30) 0 0;
        .faq_list_wrap {
            padding:0; border-color:transparent; border-bottom-color:$gray2; border:none;
            &:first-of-type {
                margin:0;
                .row_list {
                    .tit_txt {display:flex;}
                    .snb_s {
                        .toggle_icon {margin:2rem 0 0;}
                    }
                }
            }
            &:last-of-type {border-bottom-color:transparent;}
        }
        .row_list {
            .tit_txt {
                margin:0 em(30); padding:em(33) em(35) em(27); font-size:em(28);color:$gray5;font-weight:$bold;
                display:none; background-color:#f5f5f5; line-height:em(30);
                @include borderRadius(em(5));
                &:before {content:"No.";display:inline-block;min-width:em(80);}
            }
            .question_txt, .desc {font-size:em(30);line-height:em(40);font-weight:$bold;}
            .question_txt {
                width:auto; padding:em(40) em(100) em(40) em(35); display:flex; margin:0 em(30);
                .num_m {min-width:em(80); display:flex; align-items:center;}
                span {display:block;}
            }
            .snb_s {
                padding:0;
                a {
                    position:relative;
                    &.on {
                        .toggle_icon {
                            @include transform(translate(0,-50%) rotate(180deg));
                        }
                    }
                }
                .toggle_icon {
                    width:em(24); height:em(15);position:absolute;left:auto;right:em(40);top:50%; margin:0;
                    background:url(./../../images/common/models_arr.png) 50% 50% no-repeat;background-size:cover;
                    @include transform(translate(0,-50%));
                }
            }
            .num_txt {display:none;}
            .hidden_elm {
                p {
                    padding:em(50) em(30) em(40) em(45);line-height:em(40);font-size:em(30);
                    &:after {display:none;}
                }
            }
            .answer_txt {
                padding:0;color:$gray7;font-weight:$regular;font-size:em(30); background-color:$gray1;
                em {
                    text-align:center; min-width:em(60);min-height:em(60);margin:em(-10) em(25) 0 0;
                    font-size:em(28); padding:em(5) 0; font-weight:$bold;display:flex;
                    align-items:center;justify-content:center;
                    @include boxSizing;
                    @include borderRadius(em(50));
                }
            }
        }
    }
}
//NHA-COMMON-PRODUCT DOCUMENTATION-034
.product_documentation {
    //width:1200px; margin:0 auto; padding:160px 0 100px;
    h3 {font-size:20px; font-weight:$medium; color:#121314; margin:0 0 40px;}
    .series_list {
        margin:50px 0 0; font-size:16px;
        &:nth-of-type(1) {margin:0;}
        p {
            color:$gray5; background-color:$gray1; line-height:44px; padding:0 0 0 30px;
            @include borderRadius(5px)
        }
        ol {
            padding:20px 0 0;
            @extend %clearfix;
        }
        li {
            width:220px; padding:30px 0 0 30px; float:left;
            @include boxSizing;
        }
        a {
            color:$gray7; border-bottom:2px solid #a6a6a6; display:inline-block; font-weight:$medium;
            @include transition;
            &:hover,
            &:focus {color:$blue; border-bottom-color:$blue;}
        }
    }
}
