@charset "UTF-8";
// 검색 결과를 찾을 수 없음
.no_results_found_cont {
    height:calc(100vh - 363px);display:flex;align-items:center;justify-content:center;
    .no_results_txt {font-size:14px;color:$gray5;font-weight:$regular;}
    @include mob() {
        .no_results_txt {font-size:em(25);}
    }
}
// NHA-COMMON-ATM_EXPLORE-005
.item_list_type01 {
    .kind {
        font-size:12px; display:block; padding:5px 12px; margin:5px 4px 0 0; font-weight:$medium;
        float:left;
        @include borderRadius(10px);
        &.variety_1 {color:$blue6;background-color:#e2e6fd;}
        &.variety_2 {color:$blue7;background-color:#d9d9ec;}
        &.variety_3 {color:$blue5;background-color:$blue3;}
    }
    .variety_type {
        margin:-8px 0 0;
        @extend %clearfix;
    }
    .substance {
        strong {font-size:24px;color:$gray7; display:block;}
        .explain {font-size:14px;font-weight:$regular;color:$gray5; line-height:22px;}
    }
    .row_list {
        width:100%; display:flex; padding:20px 30px 20px 55px; margin:6px 0; justify-content:space-between;
        @include boxSizing;
        &.active {border:2px solid $gray7;}
        .product_thumb {
            position:relative; display:flex; max-width:560px;
            @extend %clearfix;
            .form_check_radio_default {
                position:absolute;left:-35px; top:0;
                input[type="checkbox"] {
                    &:focus ~ label:before {border-color:$blue5;}
                    &:hover ~ label:before {border-color:$blue5;}
                    &:checked ~ label:before {border:none; background-color:$blue5;}
                    & + label {
                        padding:0; position:absolute;
                        &:before {
                            border:1px solid $gray3;
                            @include transition(border, .2s);
                        }
                    }
                }
            }
            .img_item {
                min-width:195px; width:28%; display:flex; align-items:center; background-color:$white;
                @include borderRadius(10px);
                img { width:100%; }
            }
            .substance {
                padding:25px 0 0 30px;
                strong {
                    padding:15px 0 0; font-size:24px;color:$gray7; display:block;
                    &:hover {
                        a {color:$blue5;}
                    }
                }
                .explain {width:75%;font-size:14px;font-weight:$regular;color:$gray5; line-height:22px;padding:16px 0 0;}
            }
        }
        .skill_list {
            width:100%;max-width:590px; justify-content:space-between; display:flex;text-align:center;
            align-items:start; flex-wrap:nowrap; padding:50px 0 0;
            & > div {
                min-width:140px; width:inherit;
                img {width:60%;max-width:45px;}
            }
            p {width:auto; padding:20px 0 0;font-size:14px;color:$gray6; line-height:16px; margin:0 auto;}
        }
    }
    @include mob () {
        .kind {
            font-size:em(22); padding:em(13) em(15) em(7); margin:em(8) em(5) 0 0; font-weight:$bold;
            @include borderRadius(em(50));
        }
        .row_list {
            display:block; padding:em(40) 0 em(20) em(100); margin:0; border-top:em(2) solid $gray2;
            &:last-of-type {
                border-bottom:em(2) solid $gray2;
                &.active {border-bottom:em(2) solid $gray2;border-top:em(2) solid $gray2;}
            }
            &.active {border:none;}
            .product_thumb {
                display:block; max-width:inherit;
                .form_check_radio_default {
                    left:em(-70);
                    input[type="checkbox"] {
                        & + label { &:before {border:em(2) solid $gray3;} }
                    }
                }
                .img_item {
                    width:40%; min-width:inherit; float:left; border:em(2) solid $gray2;
                    @include boxSizing;
                    @include borderRadius(em(10));
                    img { width:100%; }
                }
                .substance {
                    width:60%; float:left; padding:0 em(30); margin:em(8) 0 0;
                    @include boxSizing;
                    strong {padding:em(22) 0 0;font-size:em(42);}
                    .explain {width:100%;font-size:em(28);line-height:em(38);padding:em(18) 0 0;}
                }
            }
            .skill_list {
                max-width:inherit; padding:em(50) 0 em(20); overflow-x:auto; justify-content:start;
                & > div {
                    min-width:em(185); max-width:em(100);
                    img { max-width:inherit; width:100%; }
                }
                p {padding:em(20) 0 0;font-size:em(24);line-height:em(26);}
            }
        }
    }
}
// NHA-COMMON-COMPARE-008
.popup.compare_popup {
    text-align:center;
    .popup_content {
        max-width:1200px; background-color:$white;
        @include borderRadius(27px);
        .product_title_txt {
            strong {padding:45px 0 0;display:block;font-size:30px;color:$gray7;}
            p {padding:12px 0 0;color:$gray5;font-size:16px;font-weight:$regular;}
        }
        .product_thumb_list {
            padding:45px 80px 40px;
            ul {display:flex;justify-content:center;}
            .img_item {
                width:59.34%;margin:0 auto;background-color:#f5f5f5;
                @include borderRadius(10px);
            }
            img {width:100%;}
            strong {display:block;padding:20px 0 0;font-size:26px;color:$gray7;}
            p {padding:10px 0 0;font-size:14px;color:$gray5;}
            .btn_view {
                padding:25px 0 0;display:flex;justify-content:center;
                button {
                    width:106px;border:1px solid $gray3;font-size:13px;display:block;padding:13px 0; font-weight:$medium;
                    @include borderRadius(50px);
                    @include boxSizing;
                    &:nth-of-type(1) {color:$gray7;}
                    &:nth-of-type(2) {border:none;color:$white;background-color:$blue5;margin:0 0 0 4px;}
                }
            }
        }
        .explanation_list {
            padding:10px 10px 30px; border-top:1px solid #e5e5e5;
            .scroll_wrap {padding:0 70px 20px;overflow-y:scroll;height:290px;}
            .desc_cont {
                padding:30px 0 0;text-align:left;
                &.operation_item {
                    span {
                        color:$gray7; position:relative;
                        &:after {
                            width:11px; height:10px;content:""; display:block;position:absolute;top:5px;right:-16px;
                            background:url(../../images/common/operation_icon.png) 50% 50% no-repeat; background-size:cover;
                        }
                    }
                    li {font-weight:$bold;}
                }
            }
            .description {font-size:14px; color:$gray5; font-weight:$medium; }
            ul {
                display:flex;
                p { color:$gray7; font-size:14px;font-weight:$medium; }
            }
            li {
                width:100%; font-size:14px; padding:20px 0;line-height:20px;
                display:flex;justify-content:center;align-items:center;
                text-align:center;background-color:#f5f5f5;margin:10px 0 0 5px; font-weight:$regular;
                &:first-of-type {margin:10px 0 0;}
            }
        }
    }
    .btn_popup_close {
        width:20px; height:20px; background-image:url(../../images/common/close_x_btn.png); right:26px; top:28px;
    }
}
