                    span {
                        width:100%; position:relative; cursor:pointer;
                        &:after {
                            width:em(24); height:em(15);content:"";display:block;; position:absolute;
                            right:em(10); top:50%; background:url(./../../images/common/models_arr.png) 50% 50% no-repeat;
                            background-size:cover;
                            @include transition(all, .3s);
                            @include transform(translate(0,-50%));
                            @include transform(rotate(180deg));
                        }
                    }
                    p {
                        margin:em(40) 0 0; padding:em(30);text-align:left;text-overflow:inherit;
                        -webkit-box-orient:inherit; background-color:#f5f5f5; font-size:em(28);
                        font-weight:$medium; line-height:em(42);
                        @include borderRadius(em(10));
                    }
                }
            }
            .more_btn {display:none;}
        }
    }
}
