            }
            .txt {
                p {
                    font-size:16px; padding:130px 0 0; position:relative; color:$gray7;
                    &:before {
                        width:152px; height:106px; position:absolute; top:0; left:50%; content:""; background-image:url(../../images/common/login_icon.png);
