.software_list_type03 {
    .order_box {
        width:100%; border-bottom:2px solid $gray2; box-sizing:border-box; background-color:$white; position:fixed; top:80px; left:0; z-index:90;
        .order_box_wrap {
            max-width:1200px; height:118px; margin:0 auto; position:relative;
            .product_name {
                padding:32px 0;
                p {
                    font-weight:$medium;
                    &:nth-of-type(1) {font-size:16px; color:$gray5;}
                    &:nth-of-type(2) {font-size:28px; color:$gray7; padding:12px 0 0;}
                }
            }
            .order_btn {
                position:absolute; top:50%; right:9px; text-align:center;
                @include translate(0, -50%);
                a {
                    width:116px; height:50px; line-height:50px; background-color:$blue5; font-size:16px; color:$white; font-weight:$medium; display:inline-block;
                    @include borderRadius(25px);
                    @include boxShadow(5px 5px 10px rgba(15, 13, 13, .15));
                    @include transition(background-color, .3s);
                    &:hover {background-color:$blue6;}
                }
            }
        }
    }
    .product_info {
        margin:200px 0 0; padding:70px 0 0;
        .product_info_wrap {
            max-width:1200px; margin:0 auto; padding:0 0 60px;
            h3 {font-size:26px; color:$gray7; font-weight:$medium;}
            & > p {font-size:16px; color:$gray7; line-height:24px; padding:20px 0 0;}
            .product_info_list {
                padding:50px 0 0;
                @extend %clearfix;
                .left_area {float:left;}
                .right_area {float:right;}
                .left_area, .right_area {
                    width:calc(50% - 20px);
                    & > div {
                        &:nth-of-type(n+2) {padding:40px 0 0;}
                        & > p {font-size:16px; color:$gray5; font-weight:$medium;}
                    }
                    .description_box {
                        margin:22px 0 0; padding:15px 30px; background-color:$gray1;
                        @include borderRadius(6px);
                        p {font-size:14px; color:$gray6; line-height:22px;}
                    }
                    .models {
                        .description_box {
                            p {min-height:152px;}
                        }
                    }
                }
            }
        }
    }
    .product_des {
        padding:0 0 94px;
        .product_des_wrap {
            max-width:1200px; margin:0 auto; padding:60px 0 0; border-top:1px solid rgba(0, 0, 0, .1); display:flex; flex-wrap:wrap; justify-content:space-between;
            dl {
                width:calc(50% - 25px);
                &:nth-of-type(n+3) {padding:30px 0 0;}
                dt {
                    font-size:22px; color:$gray7; font-weight:$medium; position:relative;
                    span {
                        font-size:16px; color:$blue5; font-weight:$extra-bold; display:block; margin:0 0 12px 0;
                        &:after {width:10px; height:3px; content:""; background-color:$blue5; display:block;}
                    }
                }
                dd {font-size:14px; color:$gray5; line-height:22px; padding:24px 0 0; min-height:132px;}
            }
        }
    }
    @include pc-small() {
        .order_box {padding:0 50px;}
    }
    @include mob() {
        .order_box {
            border-bottom:em(4) solid $gray2; padding:0 em(30); top:em(124);
            .order_box_wrap {
                height:auto; min-height:em(162);
                .product_name {
                    padding:em(27) 0 em(24);
                    p {
                        font-weight:$bold;
                        &:nth-of-type(1) {
                            width:em(234); height:em(40); font-size:em(22); color:$blue5; background-color:#dcebfe; line-height:em(40); text-align:center;
                            @include borderRadius(em(20));
                        }
                        &:nth-of-type(2) {max-width:em(500); font-size:em(40); margin:em(14) 0 0; padding:0; display:inline-block; line-height:em(52);}
                    }
                }
                .order_btn {
                    top:em(48); right:0;
                    @include translate(0, 0);
                    a {
                        width:em(192); height:em(90); line-height:em(90); font-size:em(30); box-shadow:none; font-weight:$bold;
                        @include borderRadius(em(45));
                    }
                }
            }
        }
        .product_info {
            margin:em(338) 0 0; padding:em(80) em(30) 0;
            .product_info_wrap {
                padding:0 0 em(80);
                h3 {width:em(570); font-size:em(50); font-weight:$bold;}
                & > p {font-size:em(32); color:$gray7; line-height:em(44); padding:em(36) 0 0;}
                .product_info_list {
                    padding:em(68) 0 0;
                    .left_area {float:none;}
                    .right_area {float:none; padding:em(50) 0 0;}
                    .left_area, .right_area {
                        width:100%;
                        & > div {
                            &:nth-of-type(n+2) {padding:em(50) 0 0;}
                            & > p {font-size:em(28); font-weight:$bold;}
                        }
                        .description_box {
                            margin:em(25) 0 0; padding:em(30); border-radius:0;
                            p {font-size:em(28); color:$gray7; line-height:em(40); font-weight:$medium;}
                        }
                    }
                }
            }
        }
        .product_des {
            padding:0 em(30) em(100);
            .product_des_wrap {
                padding:em(80) 0 0; border-top:em(2) solid $gray2;
                dl {
                    width:100%;
                    &:nth-of-type(n+2) {padding:em(50) 0 0;}
                    dt {
                        font-size:em(40); color:$gray7; font-weight:$bold; position:relative;
                        span {
                            font-size:em(28); margin:0 0 em(36) 0; position:relative;
                            &:after {width:em(24); height:em(4); position:absolute; bottom:em(-10);}
                        }
                    }
                    dd {font-size:em(30); line-height:em(42); padding:em(24) 0 0; min-height:0;}
                }
            }
        }
    }
}